import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: localStorage.getItem('token')
  },
  actions: {
    logout({ commit }){
      commit('logout')
      localStorage.removeItem('token')
    }
  },
  mutations: {
    logout(state){
    state.token = ''
  }
  },
  getters : {
    loggedIn: state => !!state.token
  }
})