<template>
  <div class="container">
    <h1>4 fun</h1>
    <div class="create-chall">
      <input type="text" id="create-post" v-model="name" placeholder="Chall name"><br>
      <input type="text" id="create-post" v-model="category" placeholder="Chall category"><br>
      <input type="text" id="create-post" v-model="content" placeholder="Chall content"><br>
      <input type="text" id="create-post" v-model="flag" placeholder="Chall key"><br>
      <input type="text" id="create-post" v-model="value" placeholder="Chall value"><br>
      <button @click="createChall" class="btn btn-success">New Chall</button>
    </div>
  </div>
</template>

<script>
import challenge_manage from '../challService'

export default {
  data() {
    return {
      challenges: []
    }
  },
  methods: {
    async createChall() {
      await challenge_manage.new_chall(this.name, this.category, this.content, this.flag, this.value);
      this.challenges = await challenge_manage.get_challs();
    }
  }
}
</script>

<style scoped>
button {
    margin: 10px;
}
input {
    width: 30%;
    padding: 7px;
    border: solid 1px blue;
    border-radius: 5px;
    margin: 5px;
}
</style>