<template>
    <div class="container text-center">
      <img alt="Vue logo" src="../assets/logo.png" class="logo">
      <div class="challenges">
      <ul class="list-group">
        <li v-for="(chall) in challenges" v-bind:key="chall._id" class="list-group-item">
          {{ chall.name }}
        </li>
      </ul>
      <p>{{ error }}</p>
      <span><a @click="logout">Logout</a></span>
      </div>
    </div>
</template>

<script>
import challenge_manage from '../challService'

export default {
  data() {
    return {
      challenges: [],
      error: ''
    }
  },
  async created() {
    try {
      this.challenges = await challenge_manage.get_challs();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    logout() {
      this.$store.dispatch('logout')
      this.$router.push('/login')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.container {
  max-width: 800px;
  margin: 0 auto;
}
p.error {
  border: 1px solid #ff5b5f;
  background-color: #ffc5c1;
  padding: 10px;
  margin-bottom: 15px;
}
button {
    margin: 10px;
}
input {
    width: 30%;
    padding: 7px;
    border: solid 1px blue;
    border-radius: 5px;
    margin: 5px;
}
.logo {
    margin-bottom: 5%;
}
</style>
